<template>
  <div class="progress">
    <el-progress 
    type="dashboard" 
    :percentage="cpercentage" 
    :color="colors"/>
    <span></span>
  </div>  
</template>

<script>
export default {
  name: 'Progress',
  data() {
    return {
      colors: [
        {color: '#f56c6c',percentage:20},
        {color:'#e6a23c',percentage:40}, 
        {color:'#5cb87a',percentage:60},
        {color:'#1989fa',percentage:80},
        {color:'#6f7ad3',percentage:100}
      ],
    }
  },
  props: {
    cpercentage: {
      type: Number,
      default() {
        return 0
      }
    }
  }
}
</script>

<style>
  .progress {
    width: 126px;
    padding-bottom: 20px;
  }
  .el-progress-circle {
    width: 180px;
    height: 180px;
  }
  .select-el-progress span {
    margin: 0 31px;
  }
</style>